.login {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	padding: 30px;
	@include cover;
	@include m {
		min-height: auto;
		padding: 0;
		background: none !important; }
	&:before {
		content: "";
		@include coverdiv;
		background: linear-gradient(301.63deg, rgba(58, 62, 69, 0.0001) 29.5%, rgba(27, 29, 33, 0.707195) 74.66%);
		@include m {
			display: none; } }
	&__container {
		position: relative;
		z-index: 2;
		display: flex;
		align-items: center;
		width: 100%;
		max-width: 960px; }
	&__wrap {
		flex-shrink: 0;
		width: 240px;
		margin-right: auto;
		@include m {
			display: none; } }
	&__form {
		position: relative;
		flex-shrink: 0;
		width: 464px;
		padding: 48px 48px 64px;
		border-radius: 40px;
		background: $wh;
		@include t {
			width: 400px;
			padding: 32px; }
		@include m {
			width: 100%;
			padding: 40px 32px;
			border-radius: 0;
			background: none; }
		&:before {
			content: "";
			position: absolute;
			top: 30px;
			left: 14px;
			right: 14px;
			bottom: -30px;
			z-index: -1;
			background: $bl;
			opacity: .8;
			filter: blur(32.6194px);
			border-radius: 40px;
			@include m {
				display: none; } } }
	&__logo {
		display: inline-block;
		margin-bottom: 27px;
		font-size: 0; }
	&__info {
		margin-bottom: 32px;
		margin-right: -20px;
		font-size: 16px;
		font-weight: 700;
		color: $wh; }
	.games {
		margin-bottom: 22px; }
	.games__item {
		background: rgba($bl, .5);
		color: $wh;
		&:hover {
			background: rgba($bl, .35); }
		@include nl {
			margin-bottom: 16px; } }
	&__add {
		display: inline-flex;
		align-items: center;
		margin-bottom: 48px;
		font-weight: 700;
		color: $wh;
		transition: opacity .25s;
		.icon {
			margin-right: 12px;
			font-size: 20px;
			fill: $wh; }
		&:hover {
			opacity: .8; } }
	&__flex {
		display: flex;
		align-items: center; }
	.switch {
		margin-right: auto; }
	&__download {
		font-size: 0;
		.icon {
			font-size: 20px;
			fill: $wh;
			transition: fill .25s; }
		&:hover .icon {
			fill: rgba($wh, .75); } }
	&__close {
		position: absolute;
		top: 24px;
		right: 24px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		width: 48px;
		height: 48px;
		border-radius: 50%;
		box-shadow: 0 2px 7px rgba(#000, .1);
		font-size: 0;
		transition: all .25s;
		@include m {
			top: 48px; }
		.icon {
			font-size: 12px;
			fill: $text;
			transition: fill .25s; }
		&:hover {
			box-shadow: 0 5px 15px rgba(#000, .1);
			.icon {
				fill: $purple; } } }
	&__title {
		margin-bottom: 20px; }
	&__line {
		display: flex;
		align-items: center;
		margin-bottom: 42px;
		font-size: 12px;
		font-weight: 500; }
	&__text {
		color: $gray-dark; }
	&__line &__link {
		margin-left: 16px;
		color: $blue;
		transition: color .25s;
		&:hover {
			color: $blue-dark; } }
	.field {
		@include nl {
			margin-bottom: 24px; } }
	&__or {
		padding: 24px 0;
		font-size: 12px;
		font-weight: 500;
		color: $gray-dark; }
	&__row {
		display: flex;
		margin: 0 -8px;
		@include nl {
			margin-bottom: 24px; } }
	&__col {
		flex: 0 0 calc(50% - 16px);
		width: calc(50% - 16px);
		margin: 0 8px; } }

@include dark {
	.login {
		&__form {
			background: $dark1;
			@include m {
				background: $dark; } }
		.games__item {
			background: rgba($bl, .5); }
		&__download {
			.icon {
				fill: $gray-light; }
			&:hover .icon {
				fill: $wh; } }
		&__close {
			background: $dark;
			@include m {
				background: $dark1; }
			.icon {
				fill: $wh; }
			&:hover {
				box-shadow: none;
				.icon {
					fill: $purple; } } } } }

