// media queries
@mixin lpc {
	@media only screen and (max-width: "1620px") {
		@content; } }
@mixin w {
	@media only screen and (max-width: "1442px") {
		@content; } }
@mixin x {
	@media only screen and (max-width: "1339px") {
		@content; } }
@mixin d {
	@media only screen and (max-width: "1259px") {
		@content; } }
@mixin t {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m2 {
	@media only screen and (max-width: "792px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "767px") {
		@content; } }
@mixin a {
	@media only screen and (max-width: "639px") {
		@content; } }
@mixin s {
	@media only screen and (max-width: "474px") {
		@content; } }

// fonts
@mixin fi {
	font-family: 'Inter', sans-serif; }
@mixin fp {
	font-family: 'TT Norms', sans-serif; }
@mixin ft {
	font-family: 'TT Norms', sans-serif !important; }
@mixin fn {
	font-family: 'Nunito', sans-serif !important; }

$bl: #1B1D21;
$wh: #ffffff;
$purple: #6C5DD3;
$purple-light: #CFC8FF;
$text: #11142D;
$gray-dark: #808191;
$gray-light: #B2B3BD;
$orange: #FF754C;
$pink: #FFA2C0;
$pink-light: #FFEBF6;
$rate-default: #e0e2ec;
$rate: #fec92c;
$yellow: #FFCE73;
$yellow2: #ffc95a;
$yellow-light: #FFE2AC;
$blue: #3F8CFF;
$blue-light: #A0D7E7;
$blue-light2: #7d8de9;
$blue-dark: #0049C6;
$border: #E4E4E4;
$border-light: #F0F3F6;
$green: #7FBA7A;
$green-2: #05ac31;
$green-light: #e5f6ea;
$link: #5F75EE;
$metro: #009734;
$red: #ff6248;

// dark theme
$dark: #1F2128;
$dark1: #242731;
$border-dark: rgba($border, .1);

@mixin shadow {
	background: #F0F3F6;
	opacity: .91;
	filter: blur(86px);
	border-radius: 24px; }

@mixin shadow-sm {
	background: #F0F3F6;
	filter: blur(43px);
	border-radius: 24px; }

@mixin shadow-dark {
	background: none; }

@mixin shadow-sm-dark {
	background: none; }

