.collection {
	&__head {
		display: flex;
		align-items: center;
		margin-bottom: 40px;
		@include m {
			display: block;
			margin-bottom: 24px; } }
	&__title {
		margin-right: auto;
		@include m {
			margin: 0 0 24px; } }
	&__field {
		min-width: 235px;
		margin-left: 30px;
		@include m {
			min-width: 100%;
			margin: 0; } }
	&__list {
		display: flex;
		margin: 0 -16px;
		@include d {
			flex-wrap: wrap;
			margin-top: -32px; }
		@include m {
			display: block;
			margin: 0; }
		.card {
			flex: 0 0 calc(25% - 32px);
			width: calc(25% - 32px);
			margin: 0 16px;
			@include w {
				flex: 0 0 calc(33.33% - 32px);
				width: calc(33.33% - 32px); }
			@include d {
				flex: 0 0 calc(50% - 32px);
				width: calc(50% - 32px);
				margin-top: 32px; }
			@include m {
				width: 100%;
				margin: 0; }
			&:nth-child(n+4) {
				@include w {
					display: none; }
				@include d {
					display: block; } }
			@include nl {
				@include m {
					margin-bottom: 32px; } } } }
	@include nl {
		margin-bottom: 55px;
		@include m {
			margin-bottom: 46px; } } }
