.gallery-catalog-list {
	margin: 0 -15px;
	display: flex;
	flex-wrap: wrap;
	&__item {
		position: relative;
		margin: 0 15px 30px;
		cursor: pointer;
		transition: opacity .2s;
		&:hover {
			opacity: .8;
			img {
				transform: scale3d(1.1, 1.1, 1.1); } }
		&::before,
		&::after {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%; }
		&::before {
			top: 5px; }
		&::after {
			top: 10px; }
		&,
		&-holder,
		&-img {
			width: 180px;
			height: 180px; }
		&-holder {
			overflow: hidden;
			border: 4px solid rgba(#fff, .7); }
		&-img {
			transition: transform 0.2s ease-in-out 0s; }
		&::before,
		&::after,
		&-holder {
			border-radius: 40px;
			box-shadow: 0px 4px 10px 0px rgba(107, 132, 160, 0.34), 0px 10px 31px 0px rgba(237, 239, 242, 0.004); } } }
@include dark {
	.gallery-catalog-list {
		&__item {
			&-holder {
				border: 4px solid rgba(#6f6f6f, .7); }
			&::before,
			&::after,
			&-holder {
				box-shadow: 0px 4px 11px 0px rgba(18, 18, 21, 0.85), 0px 10px 31px 0px rgba(37, 38, 39, 0.55); } } } }




