.block-catalog-actions {
	.block-catalog-action {
		box-shadow: 0px 6px 20px 0px rgba(#5b5b5b, .15);
		border-radius: 12px;
		border: 2px solid rgba($yellow2, .8);
		position: relative;
		max-width: 300px;
		padding: 20px 30px 20px 87px;
		margin: 15px;
		&-list {
			display: flex;
			flex-flow: wrap;
			margin: -15px -15px 0 -15px; }
		&__icon {
			width: 44px;
			height: 44px;
			border-radius: 50%;
			background-color: $yellow2;
			position: absolute;
			top: 25px;
			left: 20px;
			display: flex;
			align-items: center;
			justify-content: center;
			color: #fff;
			.svg-action-icon {
				fill: none;
				stroke-width: 2px;
				stroke: currentColor;
				font-size: 20px;
				display: block; } }
		&__text {
			@include ft; }
		&__title {
			font-weight: 700;
			font-size: 24px;
			&-small {
				font-size: 16px; } } } }
@include dark {
	.block-catalog-actions {
		.block-catalog-action {
			border: 2px solid rgba($yellow2, .42);
			background-color: rgba($border, .05);
			&__icon {
				color: $bl; }
			&__text {
				color: #fff; } } } }


/**
 * Вкладки сторінки перегляду садочка
 */
.tab-catalog {}

.tab-catalog-reviews {
	.block-title {
		margin-bottom: 24px;
		.block-title-light {
			color: $gray-light;
			font-weight: 400;
			font-size: 16px;
			@include ft;
			.block-title-sign-divider {
				margin: 0 14px; } } }
	.chat-form {
		margin: 30px 0 45px; }
	.messages {
		&-btns {
			text-align: center;
			margin-top: 45px;
			justify-content: center; } } }



.tab-catalog-gallery {}

.tab-catalog-price {
	.block-title {
		margin-bottom: 18px; } }

.tab-catalog-contacts {
	.block-contacts-catalog {
		margin-bottom: 56px; }
	&__abs-img {
		position: absolute;
		top: -12px;
		left: 37%;
		@include lpc {
			left: 475px; }
		@include d {
			display: none; } } }

.page__slider.page-catalog {
	padding-top: 0 !important; }


.page-catalog {
	&-tabs,
	&-header {
		padding: 0 64px; }
	&-header {
		background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(244,245,248,0.8) 100%);
		position: relative;
		min-height: 432px;
		padding-top: 68px;
		margin-bottom: -10px;
		&__side-wrapper {
			display: flex;
			justify-content: space-between;
			z-index: 5;
			position: relative; }
		&__meta {
			display: flex;
			align-items: start;
			.tag-small {
				padding: 5px 11px;
				border-radius: 7px;
				font-size: 10px;
				margin-right: 6px;
				line-height: 1;
				margin-top: 0;
				min-height: 21px;
				font-weight: 600;
				&--vip {
					padding-right: 12px;
					padding-left: 12px; }
				&--cat {}
				&--licence {} } }
		&__rate {
			&:hover .rate-item-info-mid,
			&:hover .rate-item-info,
			&:hover {
				color: $purple; }
			.rate-item-info-mid {
				position: relative;
				top: -1px;
				padding-left: 4px; }
			.rate-item-info {
				padding-left: 4px; } }
		&__map {
			margin-top: 14px;
			&.page-catalog-map {
				.page-catalog__map-value {
					color: $gray-light;
					font-size: 14px;
					font-weight: 400;
					margin-right: 44px;
					&.dot-divider:before {
						right: -26px;
						top: 48%;
						background-color: $gray-light; }
					&.map-value-district {
						margin-right: 30px;
						&.dot-divider:before {
							right: -15px; } }
					.svg-map-pin {
						width: 13px;
						height: 18px;
						margin-right: 4px;
						top: -1px; }
					.svg-metro {
						width: 18px;
						height: 18px;
						margin-right: 6px;
						top: -2px; } } } }
		&__sideinfo {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			margin-top: 10px; }
		&__social {
			display: flex;
			margin-right: 28px;
			&-item {
				padding: 12px;
				.svg-social-item {
					color: $gray-light;
					fill: none;
					stroke-width: 2px;
					stroke: currentColor;
					font-size: 22px;
					display: block;
					transition: all .2s; }
				&:hover {
					.svg-social-item {
						color: $blue; } } } }
		&__logo {
			&.ava {
				width: 92px;
				height: 92px; } }
		&__rate {
			margin: 4px 0 0 6px; }
		&__title {
			font-size: 45px;
			font-weight: 700;
			@include fn;
			margin-top: 16px; }
		&__bottom {
			position: relative;
			z-index: 5;
			margin-top: 52px;
			display: flex; }
		&__btns {
			display: flex;
			margin: 0 -12px;
			.btn {
				padding: 20px 25px;
				font-size: 14px;
				font-weight: 500;
				margin: 0 12px 15px;
				min-width: auto;
				@include ft;
				.svg-in-btn {
					fill: none;
					stroke-width: 2px;
					stroke: currentColor;
					font-size: 20px;
					display: block; }
				&-order {}
				&-bookmark {}
				&-with-icon {
					padding: 20px 22px;
					background-color: #eeeeee;
					transition: all .2s;
					color: $gray-dark;
					&:hover {
						background-color: $purple;
						color: #fff; } } } }
		&__gallery {
			margin: -6px 0 0 auto;
			&.gallery-list--no-btns {
				margin: 0;
				.js-catalog-page {
					max-width: 525px; } } }
		&__pattern {
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 165px;
			background-image: url(../img/catalog-page.png);
			background-repeat: repeat-x;
			background-position: bottom left;
			background-size: contain; }
		&__shadow {
			z-index: 1;
			position: absolute;
			left: 0;
			bottom: -30px;
			height: 30px;
			width: 100%;
			background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 11%, rgba(255,255,255,0) 100%);
			opacity: .03; } }
	&-tabs {
		border-bottom: 1px solid #e4e4e4;
		margin-bottom: 30px;
		@include t {
			padding-left: 40px;
			padding-right: 40px; }
		@include m {
			padding: 0 24px 0; }
		&_pt0 {
			padding-top: 0;
			@include m {
				padding-top: 0; } }
		&__menu {
			@include ft;
			display: flex;
			margin: 0 -14px;
			flex-flow: row wrap;
			@include s {
				margin: 0 -10px; } }
		&__item {
			font-size: 13px;
			color: #afb0ba;
			padding: 34px 0 24px;
			margin: 0 14px;
			display: flex;
			position: relative;
			transition: color .2s;
			@include s {
				margin: 0 10px; }
			&::before {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 4px;
				border-radius: 4px 4px 0 0;
				background-color: transparent; }
			&:hover {
				color: $text; }
			&-owner {
				order: 999;
				margin-left: auto;
				@include m {
					display: none; } }
			&.active {
				color: $text;
				&::before {
					background-color: $purple; } } } }
	.page__content {
		margin-top: 16px;
		padding-right: 60px;
		@include t {
			padding-right: 0; }
		.block-max-width {
			max-width: 600px; } }
	.page__sidebar {
		position: relative;
		padding-left: 58px;
		padding-top: 8px;
		padding-bottom: 45px;
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 100%;
			border-left: 1px solid #e4e4e4;
			margin-top: -30px; }
		@include t {
			padding-left: 0;
			max-width: 100%;
			&:before {
				height: 0;
				width: 100%;
				border-top: 1px solid #e4e4e4;
				border-left: none; }
			.messages {
				&-btns {
					&--row {
						align-items: center;
						.btn {
							max-width: 400px;
							width: 100%; } } } } } }
	&-wrapper {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		@include t {
			flex-direction: column; } }
	&__desc {
		margin-bottom: 50px;
		max-width: 1100px; }
	&__map {
		display: flex;
		&-value {
			color: $bl;
			font-size: 16px;
			font-weight: 500;
			display: flex;
			margin-right: 40px;
			@include ft;
			letter-spacing: .2px;
			&.dot-divider:before {
				right: -22px; }
			&-wrapper {
				display: flex; }
			&-metro {
				color: $metro; }
			svg {
				width: 24px;
				height: 20px;
				fill: currentColor; }
			.svg-map-pin {
				margin-right: 6px; }
			.svg-metro {
				margin-right: 8px; } }
		&-iframe {
			height: 460px;
			width: 100%;
			border-radius: 20px;
			display: block;
			&-holder {
				border-radius: 20px;
				margin-top: 36px;
				margin-bottom: 44px;
				position: relative;
				background: $wh;
				transition: box-shadow .25s;
				-webkit-tap-highlight-color: rgba(0,0,0,0);
				&:before {
					content: "";
					position: absolute;
					top: 15px;
					left: 7px;
					right: 7px;
					bottom: -15px;
					z-index: -1;
					transition: background .25s;
					@include shadow-sm; } } } }
	.block-seo {
		margin-top: 66px;
		margin-bottom: 60px; } }


@include dark {
	.page-catalog {
		&-tabs {
			border-color: rgba($border, .1);
			&__item {
				&:hover,
				&.active {
					color: #fff; } } }
		&__map {
			&-value {
				color: $gray-light; }
			&-iframe {
				&-holder {
					background: $dark1;
					&:before {
						@include shadow-sm-dark; } } } }
		&-header {
			background: linear-gradient(180deg, rgba($bl, 1) 0%, rgba(#000, .8) 100%);
			&__pattern {
				background-image: url(../img/catalog-page_w.png);
				opacity: .7; } }
		.page__sidebar {
			&:before {
				border-color: rgba($border, .1); } } } }


.catalog {
	&__nav {
		display: flex;
		margin-bottom: 42px;
		@include d {
			margin-bottom: 32px; }
		@include m {
			margin-bottom: 24px; } }
	&__nav &__link {
		position: relative;
		padding: 12px 24px;
		@include fp;
		font-size: 18px;
		line-height: (24/18);
		font-weight: 500;
		color: $text;
		transition: color .25s;
		@include m {
			padding: 12px 16px;
			font-size: 14px; }
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			width: 0;
			height: 4px;
			border-radius: 2px;
			background: $blue;
			transition: width .25s; }
		&:hover {
			color: $gray-dark; }
		&.active {
			color: $text;
			&:before {
				width: 100%; } }
		&:first-child {
			padding-left: 0; } }
	&__tags {
		display: flex;
		flex-wrap: wrap;
		margin: -8px 0 0 -8px;
		@include m {
			flex-wrap: nowrap;
			margin: 0 -24px;
			overflow: auto;
			overflow-x: auto;
			-ms-overflow-style: none;
			scrollbar-width: none;
			-webkit-overflow-scrolling: touch;
			&::-webkit-scrollbar {
			  display: none; }
			&:before,
			&:after {
				content: "";
				display: block;
				flex-shrink: 0;
				width: 24px;
				height: 1px; } } }
	& > &__tags {
		margin-bottom: 40px; }
	&__tag {
		min-width: 200px;
		margin: 8px 0 0 8px;
		border-radius: 8px;
		background: rgba($purple, .1);
		text-align: center;
		line-height: 56px;
		font-size: 13px;
		font-weight: 700;
		color: $blue;
		transition: opacity .25s;
		@include m {
			flex-shrink: 0;
			min-width: auto;
			margin: 0;
			padding: 0 35px; }
		&:first-child {
			background: $purple;
			color: $wh; }
		@include nl {
			@include m {
				margin-right: 8px; } }
		&:hover {
			opacity: .8; } }
	&__row {
		display: flex;
		@include d {
			display: block; } }
	&__container {
		flex-grow: 1; }
	&__sidebar {
		display: none;
		flex-shrink: 0;
		width: 240px;
		margin-left: 32px;
		@include m {
			display: block;
			width: 100%;
			margin: 32px 0 0; } }
	&__title {
		margin-bottom: 32px;
		@include m {
			margin-bottom: 24px; } }
	&__sorting {
		display: flex;
		align-items: center;
		margin-bottom: 32px;
		@include m {
			display: block;
			margin-bottom: 24px; }
		.checkbox {
			margin-right: 20px;
			@include m {
				display: none; } }
		.field {
			min-width: 200px;
			@include m {
				min-width: 100%; }
			@include nl {
				@include m {
					margin-bottom: 16px; } } } }
	&__box {
		display: flex;
		align-items: center;
		&:nth-child(2) {
			@include m {
				display: none; } }
		@include nl {
			margin-right: auto; } }
	&__actions {
		display: flex;
		align-items: center;
		@include d {
			display: none; }
		@include nl {
			margin-right: 24px;
			@include w {
				margin-right: 16px; } } }
	&__action {
		display: flex;
		align-items: center;
		height: 56px;
		padding: 0 20px;
		border-radius: 16px;
		font-weight: 600;
		color: $gray-dark;
		transition: background .25s;
		.icon {
			margin-right: 10px;
			font-size: 14px;
			fill: $text; }
		&:hover {
			background: rgba($border, .25); } }
	&__view {
		display: flex;
		align-items: center; }
	&__view &__link {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		width: 56px;
		height: 56px;
		border-radius: 16px;
		font-size: 0;
		transition: background .25s;
		.icon {
			font-size: 16px;
			fill: $text;
			transition: fill .2s; }
		&:hover .icon {
			fill: $blue; }
		&.active {
			background: $blue;
			.icon {
				fill: $wh; } } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -40px -16px 0;
		justify-content: center;
		@include m {
			display: block;
			margin: 0; }
		.catalog-item,
		.blog-item {
			flex: 0 0 calc(25% - 32px);
			width: calc(25% - 32px);
			margin: 40px 16px 0;
			max-width: 400px;
			@include lpc {
				flex: 0 0 calc(33.333% - 32px);
				width: calc(33.333% - 32px); }
			@include w {
				flex: 0 0 calc(33.333% - 32px);
				width: calc(33.333% - 32px); }
			@include d {
				flex: 0 0 calc(50% - 32px);
				width: calc(50% - 32px); }
			@include m {
				width: 100%;
				margin: 0 auto; }
			&:nth-child(n+10) {
				@include w {
					display: none; }
				@include d {
					display: block; } }
			@include nl {
				@include m {
					margin-bottom: 40px; } } }
		.game {
			flex: 0 0 calc(25% - 32px);
			width: calc(25% - 32px);
			margin: 40px 16px 0;
			@include w {
				flex: 0 0 calc(33.333% - 32px);
				width: calc(33.333% - 32px); }
			@include d {
				flex: 0 0 calc(50% - 32px);
				width: calc(50% - 32px); }
			@include m {
				width: 100%;
				margin: 0; }
			&:nth-child(n+10) {
				@include w {
					display: none; }
				@include d {
					display: block; } }
			@include nl {
				@include m {
					margin-bottom: 40px; } } } }
	&__btns {
		margin-top: 60px;
		text-align: center; }
	.widget {
		@include nl {
			margin-bottom: 64px;
			@include m {
				margin-bottom: 32px; } } }
	&.view &__list {
		display: block;
		margin: 0;
		.game {
			width: 100%;
			margin: 24px 0 0;
			&:nth-child(n+10) {
				@include w {
					display: none; }
				@include d {
					display: flex; } } } }
	&.view &__sidebar {
		display: block;
		@include d {
			display: none; } }
	&.view &__actions {
		@include w {
			display: none; } }
	&_channel &__sorting {
		margin-bottom: 40px;
		@include m {
			margin-bottom: 24px; } }
	&_channel &__tags {
		margin-right: auto; }
	&_channel &__tag {
		min-width: auto;
		padding: 0 30px;
		border-radius: 16px;
		color: $link;
		@include d {
			padding: 0 25px; }
		&:first-child {
			color: $wh; } }
	&_channel &__btns {
		margin-top: 24px; }
	&_channel &__list .game {
		@include nl {
			@include m {
				margin-bottom: 24px; } } }
	&_channel .game__details {
		padding-bottom: 24px; } }

@include dark {
	.catalog {
		&__action {
			background: $dark1;
			color: $wh;
			.icon {
				fill: $wh; }
			&:hover {
				background: rgba($dark1, .7); }
			@include nl {
				margin-right: 8px; } }
		&__view .catalog__link {
			.icon {
				fill: $wh; }
			&:hover .icon {
				fill: $blue; }
			&.active .icon {
				fill: $wh; } }
		&__nav .catalog__link {
			color: $wh;
			&:hover {
				color: $gray-dark; }
			&.active {
				color: $wh; } } } }
